// src/router/index.js
import { createRouter, createWebHashHistory } from 'vue-router';
// import HomePage from '../views/HomePage.vue'; // 导入你的页面组件
import VerificationResult from '../views/VerificationResult.vue'; // 导入另一个页面组件
import Permission from '../views/Permission.vue'; // 导入权限说明页面

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Permission,
  },
  {
    path: '/result',
    name: 'VerificationResult',
    component: VerificationResult,
  },
  {
    path: '/permission',
    name: 'Permission',
    component: Permission,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
